.inputs {
    margin: 2vh auto;
    width: 90%;
    max-width: 500px;
    justify-content: space-between;
    text-align: left;
}

.find-header{
  margin: 1em
}

input {
  width: 100%;
  padding: 1px 0;
  border: none;
  border-bottom: 1px solid black;
  font-family: sans-serif;
  font-size: 1em;
  text-align: right;
}

select {
  width: 100%;
  padding: 1px 0;
  font-family: sans-serif;
  font-size: 1em;
  text-align-last: right;
}

option {
  font-family: sans-serif;
  font-size: 1em;
  direction: rtl;
}

.inputs label input {
  display: flex;
  border-radius: 0;
}

.input-wrapper {
  margin: 2vh 0;
  position: relative;
}

.input-wrapper label {
  position: absolute
}

.beam-wrapper {
  margin: 2vh 0;;
}


.defl-wrapper {
  display: flex;
  margin: 1vh 0;
  justify-content: space-between;
}

.defl-input {
  display: inline
}



input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0;
}

input[type=number] {
    -moz-appearance: textfield;
  }

.radios-wrapper {
  margin: 1vh 0;
  justify-content: space-between;
}

.radios {
  margin-top: -1em;
  text-align: center;
  width: 100%;
  justify-content: right;
}

.radio {
  text-align: right;
}

.radio input {
  margin: 0 0.5vw;
  width: auto;
}

.defl-input {
    width: 3em;
}

.calc-button {
  width: 100%;
  background-color: var(--button);
  color: white;
  border: none;
  font-weight: bold;
  font-size: 1.5em;
  margin: 1vh auto;
  border: 1px solid var(--button);
  text-align: center;
  padding: 0.8em 0;
}

.calc-button:hover{
  background-color: var(--hover);
}